.thumb {
  height: 25vw;
  position: relative;
  overflow: hidden;
}

.thumb a {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.thumb-image {
  transition: 0.2s ease-out;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-color: rgba(190, 190, 190, 0);
  background-blend-mode: exclusion;
  opacity: 1;
}
.thumb-image:hover {
  background-color: #bebebe;
}
.thumb-image:hover > .thumb-overlay {
  opacity: 1;
}
.thumb-image:hover .thumb-title {
  margin-left: 0px;
  opacity: 1;
  transition-delay: 0s;
}
.thumb-image:hover .thumb-viewmore {
  margin-left: 0;
  opacity: 1;
  transition-delay: 0.2s;
}

.thumb-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  opacity: 0;
  background-color: #eeede7;
  mix-blend-mode: saturation;
  transition: 0.2s ease-in-out;
}

.thumb-info-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  width: 100%;
  height: 100%;
  padding: 1.6vw;
  text-transform: capitalize;
  color: #181617;
  z-index: 10;
}
.thumb-info-wrapper:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #eeede7;
  opacity: 0;
  z-index: -1;
  transition: 0.2s ease-in-out;
}
.thumb-info-wrapper:hover::after {
  opacity: 0.7;
}

.thumb-title {
  display: block;
  position: relative;
  height: 100%;
  opacity: 0;
  letter-spacing: 0;
  font-family: "Telegraf", sans-serif;
  font-weight: 700;
  text-transform: capitalize;
  font-size: clamp(14px, 130%, 25px);
  margin-left: -20px;
  transition: 0.3s ease-in-out;
}

.thumb-category {
  font-family: "IBM Plex Mono", monospace;
  font-weight: 500;
  font-size: clamp(7px, .7em, 14px);
  letter-spacing: 0;
  text-transform: lowercase;
}

.thumb-viewmore {
  font-family: "IBM Plex Mono", monospace;
  font-weight: 500;
  text-transform: lowercase;
  font-size: clamp(7px, .7em, 14px);
  position: absolute;
  bottom: 0;
  opacity: 0;
  margin-left: -20px;
  transition: 0.3s ease-in-out;
}

.imageContainer {
  width: 100%;
  height: 100%;
