.navToggleBtn {
  cursor: pointer;
  position: fixed;
  top: 30px;
  right: 25px;
  width: 30px;
  height: 20px;
  margin: 10px;
  display: none;
  flex-direction: column;
  justify-content: space-around;
  z-index: 101;
}

.navToggleBtn_e {
  position: absolute;
  left: 50%;
  background-color: #eeede7;
  width: 100%;
  height: 1.5px;
  transition: 0.25s ease-out;
}
.navToggleBtn_e:first-child {
  transform: translate(-50%, -5px);
}
.navToggleBtn_e:last-child {
